import { QuesService } from './../ques.service';
import { IQuestion } from './../Question';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-page',
  templateUrl: './page.component.html',
  styleUrls: ['./page.component.css']
})
export class PageComponent implements OnInit {
  selection:number=0;
  hideIntro:number=1;
  arr:IQuestion[];
  user:string;
  length:number;
  index=0;
  cnt:number=0;
  question:String;
  option1:String;
  option2:String;

  option3:String;

  option4:String;


  constructor(private _mylist:QuesService) { 
    this.arr=this._mylist.getQues();
   this.question=this.arr[0].description;
   
  }
  ngOnInit(): void {
    
  }
  fu1(){
    if(this.index==this.arr.length-1){
      this.done();
      return;
    }
    this.selection=1;
    console.log(this.arr[this.index].answer);
    if(this.selection==(this.arr[this.index].answer)){
      this.cnt=this.cnt+1;
    }
    this.index+=1;
    this.question=this.arr[this.index].description;
  }
  fu2(){
    if(this.index==this.arr.length-1){
      this.done();
      return;
    }
    this.selection=2;
    if(this.selection==this.arr[this.index].answer){
      this.cnt=this.cnt+1;
    }
    this.index+=1;
    this.question=this.arr[this.index].description;
  }
  fu3(){
    if(this.index==this.arr.length-1){
      this.done();
      return;
    }
    this.selection=3;
    if(this.selection==this.arr[this.index].answer){
      this.cnt=this.cnt+1;
    }
    this.index+=1;
    this.question=this.arr[this.index].description;
  }
  fu4(){
    if(this.index==this.arr.length-1){
      this.done();
      return;
    }
    this.selection=3;
    if(this.selection==this.arr[this.index].answer){
      this.cnt=this.cnt+1;
    }
    this.index+=1;
    this.question=this.arr[this.index].description;
  }
  back(){
    if(this.index==0){
      alert('try to go forward in life');
    }
    else{
      this.index=this.index-1;
     this.question=this.arr[this.index].description;
    }

  }
  hiding={
    "intro":true,
      "fadeOut":false
  }
  nonhiding={
    "container":true,
    "intro":false,
      "fadeOut":true
  }
  hide(){
    this.hiding.intro=false;
    this.hiding.fadeOut=true;
    this.nonhiding.fadeOut=false;
  }

  
  
  done(){
    if(this.cnt==20){
      alert('your score is '+this.cnt +"  out of 20 good job");
  }
  else{
    alert('your score is '+this.cnt +"  out of 20 you failed , are you retarded or what");
  }
    }
    


}
